/* App.css */
/* Sign up content styles */
.form-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-width: 400px;
  margin: 0 auto;
  padding: 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}



.boxPic {
  cursor: pointer;
  transition: transform 0.2s;
}

.boxPic:hover {
  transform: scale(1.05);
}
.signup-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
  margin-top: 2rem;

}

.free {
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  background-color: #333;
  color: white;
  border: none;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

}
.fee{
  align-content: center;
  justify-content: center;
  list-style-type: none
}





.signup-content h2 {
  margin-bottom: 1rem;
  padding-top: 1rem;
}

.signup-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}

.signup-form input {
  padding: 0.5rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}



.signup-form button:hover {
  background-color: #555555;
}
.footer.footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 1rem 0;
