/* Header styles */
.header {
  background-color: #333;
  color: white;
  padding: 1rem 0;
}

/* Container for main content */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1200px; /* Adjust as needed */
  margin: 0 auto;
  padding: 1rem;
  box-sizing: border-box;
}


/* Branding styles */
#branding h1 {
  margin: 0;
}


.tool {
  background-color: #333;

}
.benefits {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  margin-top: 1rem;
  border-radius: 20px;
}

@media (min-width: 768px) {
  .benefits {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
  .benefits > div {
    flex: 1 1 50%;
  }
  
  .subTitle {
    font-size: 1rem;
    font-weight: medium;
    color: gray
  }



/* Navbar styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: #333; /* Match header background */
  color: white;
  border-radius: 5px;
  width: 100%; /* Ensure full width */
}


/* Navigation links */
.nav-links {
  display: flex;
  gap: 1rem;
  list-style: none;
}
.desktop-menu {
  display: flex;
  gap: 1rem;
  list-style: none;
}

.desktop-menu a:hover {
  background-color: #555555;
}


.nav-links a {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem; /* Increased padding for easier clicking */
  border-radius: 5px;
  transition: background-color 0.3s ease;
}




/* Hamburger icon styles */
.hamburger {
  cursor: pointer;
  outline: none;
  margin-right: none;
  background-color: #333;
  width: 3rem;

  

  
}


.MenuIcon{
  width: 2rem;
  height: 0.25rem;
  color: white;
}




/* Showcase section styles */
#showcase {
  background: url('../public/images/banner.png') no-repeat center center/cover fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  box-sizing: border-box; /* Ensure margin is included in height calculation */
}

/* Media query for mobile devices */
@media (max-width: 768px) {
  #showcase {
    background: url('../public/images/banner.png') no-repeat center center/cover;
    height: 50vh; /* Adjust height for mobile */
    width: 100vw;
    background-attachment: scroll; /* Disable fixed background on mobile */
    margin-top: 60px; /* Ensure margin is consistent on mobile */
  }
}



/* Main content area */
main.container {
  padding: 2rem;
}

/* Card component styles */
.card {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 1rem;
  margin-bottom: 1rem;
}

/* Footer styles */
.footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 1rem 0;
  position: relative;
  bottom: 0;
  width: 100%;
  text-decoration: none;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
  }
  
  .nav-links {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 4rem; /* Adjust the top position for better alignment */
    right: 1rem; /* Adjust the right position for better alignment */
    background-color: #333;
    border-radius: 5px;
    z-index: 1000;
    width: 100%; /* Full width for dropdown */
  }
  
  .nav-links.open {
    display: flex;
  }
  
  .hamburger {
    display: flex;
    flex-direction: column;
    align-items: flex-end; /* Align hamburger icon to the right */
    margin-right: 1rem; /* Add margin to the right of hamburger icon */
    outline: nonex;
  }
}
